@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
* {
    font-family: 'Montserrat', sans-serif;
}

.profile {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.upper {
    display: flex;
    justify-content: center;
    width: 60%;
    align-self: flex-start;
}

.posted-image {
    width: 100%;
    height: 100%;
}

.middle {
    width: 50%;
    height: 80vh;
}

.flex-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
   
}

.middle {
    display: flex;
    justify-content: center;
    align-items: center;
}

p {
    width: 45%;
    text-align: center;
    font-size: 25px;
}

.user-link {
    display: flex;
}
.link {
    text-decoration: none;
    color: black;
}

@media screen and (max-width: 480px) {
    .middle {
        width: 100%;
        height: 50vh;
    }
    p {
        font-size: 20px;
        width: 60%;
    }

    .grid-container {
        margin-top: 40px;
    }
    .upper {
        width: 60%;
        justify-content: start;
    }
}
